'use client';
import { useEffect, useState } from 'react';
import { useToast } from "@/hooks/use-toast";
import { EventCategory } from '@/types/type';
import { events } from '@/constants/events';
import EventCard from '@/components/ui/EventCard';
import { Skeleton } from '@/components/ui/skeleton';
import { Button } from '@/components/ui/button';
import { jsPDF } from "jspdf";
import { ChevronLeft, ChevronRight, Clock3, DollarSign, Download, Flame, School, School2Icon, Search } from 'lucide-react';
import { useDebounce } from 'use-debounce';
import { Input } from '@/components/ui/input';
import { motion, AnimatePresence } from 'framer-motion';

type Student = {
    name: string;
    email: string;
}
type Event = {
    title: string;
    location: string;
    date: string;
    registeringId?: string;
}
const filters = [
    { label: "All", icon: School2Icon },
    { label: "Upcoming", icon: Flame },
    { label: "Past", icon: Clock3 },
    { label: "University", icon: School },
    { label: "Price", icon: DollarSign },
];

const UserDashboard = () => {
    const { toast } = useToast();
    const [event, setEvents] = useState<EventCategory[]>([]);
    const [activeFilter, setActiveFilter] = useState<string>('all');
    const [searchQuery, setSearchQuery] = useState('');
    const [debouncedQuery] = useDebounce(searchQuery, 300);
    const [loading, setLoading] = useState<boolean>(false);


    // status new Pagination 
    const [currentPage, setCurrentPage] = useState(1);
    const [eventsPerPage] = useState(6); // ÿπÿØÿØ ÿßŸÑÿ£ÿ≠ÿØÿßÿ´ ŸÅŸä ŸÉŸÑ ÿµŸÅÿ≠ÿ©
    const [isLoading, setIsLoading] = useState(true);

    const generateInvoice = (event: Event, student: Student) => {
        const doc = new jsPDF();

        doc.setFontSize(16);
        doc.text("Booking Invoice", 20, 20);

        doc.setFontSize(12);
        doc.text(`Student Name: ${student.name}`, 20, 40);
        doc.text(`Student Email: ${student.email}`, 20, 50);

        doc.text(`Event Name: ${event.title}`, 20, 70);
        doc.text(`Location: ${event.location}`, 20, 80);
        doc.text(`Date: ${new Date(event.date).toLocaleDateString()}`, 20, 90);

        doc.text(`Booking ID: #${event.registeringId || "123456"}`, 20, 110);

        doc.text("Thank you for booking with us!", 20, 130);

        doc.save(`Invoice-${event.title}.pdf`);
    };

    // Fillter events
    const filteredEvents = (events || []).filter((eve) => {
        const matchesSearch = eve.title.toLowerCase().includes(debouncedQuery.toLowerCase());
        const matchesFilter =
            activeFilter === 'all' ||
            (activeFilter === 'upcoming') ||
            (activeFilter === 'past') ||
            (activeFilter === 'university' && eve.location.includes("University")) ||
            (activeFilter === 'price');

        return matchesSearch && matchesFilter;
    });
    // Calculate events for the current page
    const indexOfLastEvent = currentPage * eventsPerPage;
    const indexOfFirstEvent = indexOfLastEvent - eventsPerPage;
    const currentEvents = filteredEvents.slice(indexOfFirstEvent, indexOfLastEvent);
    const totalPages = Math.ceil(filteredEvents.length / eventsPerPage);

    // Change page
    const paginate = (pageNumber: number) => setCurrentPage(pageNumber);
    useEffect(() => {
        const fetchEvents = async () => {
            setLoading(true);

            const filterType =
                activeFilter === "all"
                    ? ""
                    : activeFilter === "upcoming"
                        ? "upcoming"
                        : activeFilter === "past"
                            ? "past"
                            : "";

            const url = `${process.env.NEXT_PUBLIC_API_URL}/events?type=${filterType}&page=${currentPage}&perPage=${eventsPerPage}`;

            console.log("query : ", filterType);
            try {
                const res = await fetch(url,
                    {
                        method: "GET",
                        credentials: "include",
                    }
                );
                const data = await res.json();
                setEvents(data.event);
            } catch (error: any) {
                // Handle connection errors or unexpected errors.
                toast({
                    title: "Error",
                    description: error?.message || "An unexpected error occurred.",
                    className: "bg-red-600 text-white border-0",
                });
            } finally {
                setLoading(false);
            }
        };

        // fetchEvents();
    }, [activeFilter, currentPage]);
    return (
        <section className="px-4 py-6">
            <h1 className="text-2xl font-bold mb-6 text-center text-purple-800">üéâ My Booked Events</h1>
            <div className='flex justify-between items-center text-center my-8'>
                {/* Search Input */}
                <div className="relative flex-1 mr-4">
                    <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                        <Search className="h-5 w-5 text-purple-400" />
                    </div>
                    <Input
                        placeholder="Search events..."
                        className="pl-10 pr-4 py-2 rounded-full border border-purple-300 focus:ring-2 focus:ring-purple-500 focus:border-transparent w-full"
                        value={searchQuery}
                        onChange={(e) => {
                            setSearchQuery(e.target.value);
                            setCurrentPage(1);
                        }}
                    />
                </div>
                <div className="flex flex-wrap justify-center gap-2 relative">
                    {filters.map(({ label, icon: Icon }, index) => {
                        const filterKey = label.toLowerCase() as typeof activeFilter;
                        return (
                            <motion.button
                                key={label}
                                whileHover={{
                                    scale: 1.05,
                                    background: 'linear-gradient(45deg, #9333ea, #4f46e5)'
                                }}
                                whileTap={{ scale: 0.95 }}
                                onClick={() => setActiveFilter(filterKey)}
                                className={`relative flex items-center gap-3 px-5 py-3 rounded-full
                                            border-2 backdrop-blur-lg transition-all duration-300
                                            ${activeFilter === filterKey
                                        ? 'border-purple-500/30 bg-purple-600/20 text-white'
                                        : 'border-white/10 bg-black/95 text-purple-100 hover:bg-purple-500/20'}`}
                                style={{
                                    boxShadow: activeFilter === filterKey
                                        ? '0 4px 24px -2px rgba(165, 180, 252, 0.3)'
                                        : 'none'
                                }}
                            >
                                <Icon size={20} className="text-cyan-400" />
                                <span className="text-sm font-medium tracking-wide">{label}</span>
                                {activeFilter === filterKey && (
                                    <motion.div
                                        className="absolute -bottom-2 h-1 bg-cyan-400 rounded-full"
                                        initial={{ scaleX: 0 }}
                                        animate={{ scaleX: 1 }}
                                        transition={{ type: 'spring', stiffness: 200 }}
                                    />
                                )}
                            </motion.button>
                        );
                    })}
                </div>
            </div>
            {isLoading ? (
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                    {[...Array(3)].map((_, i) => (
                        <Skeleton key={i} className="h-[350px] w-full rounded-xl" />
                    ))}
                </div>
            ) : events.length === 0 ? (
                <p className="text-center text-gray-500 mt-10">You haven't booked any events yet.</p>
            ) : (
                <AnimatePresence>
                    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                        {currentEvents.map((event, index) => (
                            <EventCard key={event.id} event={event} className="">
                                <div className="p-4 space-y-2">
                                    <h1 className="text-2xl font-semibold text-black">Host : {event.host}</h1>
                                    <h3 className="text-xl font-semibold text-purple-700">Title : {event.title}</h3>
                                    <p className="text-sm text-gray-600">{event.description}</p>
                                    <p className="text-sm text-gray-600">registerd : {event.registerd}</p>
                                    <p className="text-sm text-gray-500">üìÖ {new Date(event.date).toLocaleDateString()}</p>
                                    <p className="text-sm text-gray-500">üìÖTime : {event.time}</p>
                                    <p className="text-sm text-gray-500">üìçLocation : {event.location}</p>
                                    <p className="text-sm text-gray-500">üìçHall : {event.hall}</p>
                                    <Button onClick={() => generateInvoice(event, student)}
                                        variant="outline"
                                        className="flex items-center gap-2 bg-purple-500 text-white">
                                        <Download size={18} />
                                        Download Invoice
                                    </Button>
                                </div>
                            </EventCard>
                        ))}
                    </div>
                </AnimatePresence>
            )}

            {/* Pagination Controls */}
            {
                filteredEvents.length > eventsPerPage && (
                    <div className="flex justify-center mt-12">
                        <nav className="flex items-center gap-2">
                            <button
                                onClick={() => paginate(Math.max(1, currentPage - 1))}
                                disabled={currentPage === 1}
                                className={`p-2 rounded-full ${currentPage === 1 ? 'opacity-50 cursor-not-allowed' : 'hover:bg-purple-500/20'}`}
                            >
                                <ChevronLeft className="text-purple-400" />
                            </button>

                            {Array.from({ length: totalPages }).map((_, index) => (
                                <button
                                    key={index}
                                    onClick={() => paginate(index + 1)}
                                    className={`w-10 h-10 rounded-full flex items-center justify-center
                                                ${currentPage === index + 1
                                            ? 'bg-purple-600 text-white'
                                            : 'text-purple-400 hover:bg-purple-500/20'
                                        } `}
                                >
                                    {index + 1}
                                </button>
                            ))
                            }

                            <button
                                onClick={() => paginate(Math.min(totalPages, currentPage + 1))}
                                disabled={currentPage === totalPages}
                                className={`p-2 rounded-full ${currentPage === totalPages ? 'opacity-50 cursor-not-allowed' : 'hover:bg-purple-500/20'} `}
                            >
                                <ChevronRight className="text-purple-400" />
                            </button >
                        </nav >
                    </div >
                )}
        </section>
    );
};

export default UserDashboard;